import React, { useState } from "react";
import styles from "./style.module.scss";
import { useTitle } from "let-hooks/lib";
import { Button, Input } from "antd";

const Base64Page = () => {
  const [encodedData, setEncodedData] = useState<string>("");
  const [decodedData, setDecodedData] = useState<string>("");
  useTitle("Base64编码/解码");

  const handleDecode = () => {
    setDecodedData(Buffer.from(encodedData, "base64").toString());
  };
  const handleEecode = () => {
    setEncodedData(Buffer.from(decodedData).toString("base64"));
  };

  return (
    <div className={styles.container}>
      <div className={styles.main}>
        <Input.TextArea
          value={decodedData}
          placeholder="Base64编码内容粘贴在这里"
          autoSize={{ minRows: 10, maxRows: 10 }}
          onChange={(e) => setDecodedData(e.target.value)}
        />
        <Input.TextArea
          value={encodedData}
          placeholder="Base64解码内容粘贴在这里"
          autoSize={{ minRows: 10, maxRows: 10 }}
          onChange={(e) => setEncodedData(e.target.value)}
        />
      </div>
      <div className={styles.actions}>
        <Button type="primary" onClick={handleEecode}>
          编码
        </Button>
        <Button type="primary" onClick={handleDecode}>
          解码
        </Button>
      </div>
    </div>
  );
};

export default Base64Page;
